import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { Validators } from "@angular/forms";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { CourseRequestService } from "app/admin/services/Admin/course-request.service";

import {
  CourseData,
  IAddCourseRequest,
  ICourses,
  IRequest,
  RequestData,
} from "app/admin/models/admin/course-request";
import { TranslateService } from "@ngx-translate/core";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";

import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-course-employee-request",
  templateUrl: "./course-employee-request.component.html",
  styleUrls: ["./course-employee-request.component.scss"],
})
export class CourseEmployeeRequestComponent implements OnInit {
  requestForm: FormGroup;
  submitted: boolean = false;
  coursesList: CourseData[];
  totalItems: number;
  displayedColumns: string[] = ["#", "courseName", "status", "actions"];
  dataSource: MatTableDataSource<RequestData>;
  curLang: string;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private fb: FormBuilder,
    private CourseRequestService: CourseRequestService,

    private translate: TranslateService,
    private dialog: MatDialog,
    private toastr: MessagesService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.requestForm = this.fb.group({
      courseId: ["", Validators.required],
      details: ["", [Validators.required, CustomeValidator.whiteSpace]],
    });

    this.getAllCourses();
    this.getAllUserRequests(1);
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
  getAllCourses() {
    this.CourseRequestService.getAllCourses<ICourses>().subscribe((res) => {
      if (res.returnValue == 1) {
        this.coursesList = res.Courses;
      }
    });
  }
  getAllUserRequests(page: number) {
    this.CourseRequestService.getAllUserRequests<IRequest>(page).subscribe(
      (res) => {
        if (res) {
          this.totalItems = res.countItems;
          this.dataSource = new MatTableDataSource(res.Requests);
        }
      }
    );
  }
  get RequestFormCtrl() {
    return this.requestForm.controls;
  }
  AddRequest() {
    this.submitted = true;

    let request: IAddCourseRequest = {
      courseId: this.RequestFormCtrl.courseId.value,
      details: this.RequestFormCtrl.details.value,
    };
    console.log(request);
    if (this.requestForm.valid) {
      this.CourseRequestService.addCourseRequest(request).subscribe(
        (res: any) => {
          if (res.returnValue == 1) {
            this.toastr.success("JobTitle.toasterAddSuccess");

            this.submitted = false;
            this.getAllUserRequests(1);
            this.requestForm.reset();
          }
        }
      );
    }
  }
  deleteCourseRequest(request: RequestData) {
    this.CourseRequestService.deleteRequest(request).subscribe((res: any) => {
      if (res.returnValue == 1) {
        let i = this.dataSource.data.findIndex((e) => e == request);
        this.dataSource.data.splice(i, 1);
        this.dataSource.paginator = this.paginator;

        this.toastr.success("JobTitle.toasterdeleteSucses");
      }
    });
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  openDeleateDialog(request: RequestData): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg:
            this.curLang == "ar"
              ? request.course_ar_name
              : request.course_en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCourseRequest(request);
      });
  }
}
