import { Component, OnInit } from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import {
  IMyCoursesMV,
  IRecAndBasicCoursesMV,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";

@Component({
  selector: "app-employee-courses",
  templateUrl: "./employee-courses.component.html",
  styleUrls: ["./employee-courses.component.scss"],
})
export class EmployeeCoursesComponent implements OnInit {
  recomCourses: IMyCoursesMV[];
  basicCourses: IMyCoursesMV[];
  curLang: string;
  constructor(
    public courseService: CourseService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.courseService
      .getBasicAndRecommendedCourses()
      .subscribe((res: IRecAndBasicCoursesMV) => {
        this.basicCourses = res.basicCoursesList;
        this.recomCourses = res.recommendedCoursesList;
      });
  }
}
